import React from 'react';
import {Container} from '../styles/container';
import Drum from './Drum';

class DrumMachine extends React.Component{
    constructor(props){
        super(props);
        this.state={
            state:null
        }
    }
    render(){
        return(
            <div id="drum-machine" className="w-100 d-flex flex-column column justify-content-center align-items-center">
            <header className="w-50 h-25 d-flex justify-content-center align-items-center">
                <h1>My Drum Machine</h1>
            </header>
            <Container className="rounded">
                <Drum/>
            </Container>
            </div>
        );
    }
}

export default DrumMachine;