import React, {useState, useEffect} from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import Volume from './Volume';
import Switch from '@material-ui/core/Switch';
import { PlayButton } from '../styles/container';


const Drum = () => {
    let [bank, setBank] = useState(false);
    let [power, setPower] = useState(true);
    let [display, setDisplay] = useState("Display");
    useEffect(() => {
        document.addEventListener("keydown", (e)=>{
            if(e.keyCode === 81){
                document.getElementById("heater-1").click();
            }else if(e.keyCode===87){
                document.getElementById("heater-2").click();
            }else if(e.keyCode===69){
                document.getElementById("heater-3").click();
            }else if(e.keyCode===65){
                document.getElementById("heater-4").click();
            }else if(e.keyCode===83){
                document.getElementById("clap").click();
            }else if(e.keyCode===68){
                document.getElementById("open-hh").click();
            }else if(e.keyCode===90){
                document.getElementById("kick-n-hat").click();
            }else if(e.keyCode===88){
                document.getElementById("kick").click();
            }else if(e.keyCode===67){
                document.getElementById("closed-hh").click();
            }
        });
    });
    const changeBank = () => {
        setBank(!bank);
    }
    const changePower = () => {
        setPower(!power);
        let audios = document.getElementsByTagName("audio");
        for (let i = 0 ; i<audios.length ; i++){
            let audio = audios[i];
            audio.pause();
            audio.currentTime= 0;
        }
    }
    const Toggle = {
        Q: () => {
            let audio = document.getElementById("Q");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Heater 1");
            }else if (power === true && bank === true){
                setDisplay("Chord 1");
            }
        },
        W: () => {
            let audio = document.getElementById("W");
            audio.pause();
            audio.currentTime=0;
            audio.play(); 
            if(power === true && bank === false){
                setDisplay("Heater 2");
            }else if (power === true && bank === true){
                setDisplay("Chord 2");
            }
        },
        E: () => {
            let audio = document.getElementById("E");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Heater 3");
            }else if (power === true && bank === true){
                setDisplay("Chord 3");
            }
        },
        A: () => {
            let audio = document.getElementById("A");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Heater 4");
            }else if (power === true && bank === true){
                setDisplay("Shaker");
            }
        },
        S: () => {
            let audio = document.getElementById("S");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Clap");
            }else if (power === true && bank === true){
                setDisplay("Open HH");
            }
        },
        D: () => {
            let audio = document.getElementById("D");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Open HH");
            }else if (power === true && bank === true){
                setDisplay("Closed HH");
            }
        },
        Z: () => {
            let audio = document.getElementById("Z");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Kick n' Hat");
            }else if (power === true && bank === true){
                setDisplay("Punchy Kick");
            }
        },
        X: () => {
            let audio = document.getElementById("X");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Kick");
            }else if (power === true && bank === true){
                setDisplay("Side-Stick");
            }
        },
        C: () => {
            let audio = document.getElementById("C");
            audio.pause();
            audio.currentTime=0;
            audio.play();
            if(power === true && bank === false){
                setDisplay("Closed HH");
            }else if (power === true && bank === true){
                setDisplay("Snare");
            }
        },
    }
    return(
        <React.Fragment>
        <Container>
            <Row className="text-center h-25">
                <Col>
                    <p>Power</p>
                    <Switch
                    checked={power}
                    onChange={changePower}
                    />
                </Col>
                <Col>
                    <p>Select your bank</p>
                    <Switch
                    onChange = {changeBank}
                    checked={bank}
                    />
                </Col>
                <Col>
                    <p>Volume</p>
                    <Volume/>
                </Col>
            </Row>
            <Row style={{marginTop:"2%"}} className="h-100 d-flex flex-row row justify-content-center align-items-center">
                <Container style={{position:"relative"}} className="w-75">
                    <Row>
                        <div
                        id = "heater-1"
                        className="drum-pad"
                        onClick={Toggle.Q}
                        >
                            <audio
                            src={bank ? "https://s3.amazonaws.com/freecodecamp/drums/Chord_1.mp3" :"https://s3.amazonaws.com/freecodecamp/drums/Heater-1.mp3"}
                            id = "Q"
                            className="clip"
                            muted={!power}
                            />
                            Q
                        </div>
                        <div
                        className="drum-pad"
                        id="heater-2"
                        onClick={Toggle.W}
                        >
                            W
                            <audio
                            src={bank ? "https://s3.amazonaws.com/freecodecamp/drums/Chord_2.mp3" :"https://s3.amazonaws.com/freecodecamp/drums/Heater-2.mp3"}
                            id = "W"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                        <div
                        className="drum-pad"
                        id="heater-3"
                        onClick={Toggle.E}
                        >
                            E
                            <audio
                            src={bank ? "https://s3.amazonaws.com/freecodecamp/drums/Chord_3.mp3" :"https://s3.amazonaws.com/freecodecamp/drums/Heater-3.mp3"}
                            id = "E"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                    </Row>
                    <Row>
                        <div
                        className="drum-pad"
                        id="heater-4"
                        onClick={Toggle.A}
                        >
                            A
                            <audio
                            src={bank? "https://s3.amazonaws.com/freecodecamp/drums/Give_us_a_light.mp3" : "https://s3.amazonaws.com/freecodecamp/drums/Heater-4_1.mp3"}
                            id = "A"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                        <div
                        className="drum-pad"
                        id="clap"
                        onClick={Toggle.S}
                        >
                            S
                            <audio
                            src={bank? "https://s3.amazonaws.com/freecodecamp/drums/Dry_Ohh.mp3" :"https://s3.amazonaws.com/freecodecamp/drums/Heater-6.mp3"}
                            id = "S"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                        <div
                        className="drum-pad"
                        id="open-hh"
                        onClick={Toggle.D}
                        >
                            D
                            <audio
                            src={bank? "https://s3.amazonaws.com/freecodecamp/drums/Bld_H1.mp3" :"https://s3.amazonaws.com/freecodecamp/drums/Dsc_Oh.mp3"}
                            id = "D"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                    </Row>
                    <Row>
                        <div
                        className="drum-pad"
                        id="kick-n-hat"
                        onClick={Toggle.Z}
                        >
                            Z
                            <audio
                            src={bank? "https://s3.amazonaws.com/freecodecamp/drums/punchy_kick_1.mp3" :"https://s3.amazonaws.com/freecodecamp/drums/Kick_n_Hat.mp3"}
                            id = "Z"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                        <div
                        className="drum-pad"
                        id="kick"
                        onClick={Toggle.X}
                        >
                            X
                            <audio
                            src={bank ? "https://s3.amazonaws.com/freecodecamp/drums/side_stick_1.mp3" :"https://s3.amazonaws.com/freecodecamp/drums/RP4_KICK_1.mp3"}
                            id = "X"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                        <div
                        className="drum-pad"
                        id="closed-hh"
                        onClick={Toggle.C}
                        >
                            C
                            <audio
                            src={bank? "https://s3.amazonaws.com/freecodecamp/drums/Brk_Snr.mp3" :"https://s3.amazonaws.com/freecodecamp/drums/Cev_H2.mp3"}
                            id = "C"
                            className="clip"
                            muted={!power}
                            />
                        </div>
                    </Row>
                </Container>
                <Container id="display" className="w-25 d-flex justify-content-center align-items-center">
                    {power ? display: "DrumPad is off!"}
                </Container>
            </Row>
        </Container>
        </React.Fragment>
    );
}



export default Drum;